        return true
      }
      return false
    },
    isDeceasedType5(deceasedPeople) {
      if(deceasedPeople === '장인장모') {
        return true
      }
      return false
    },
    getCongratulatoryOfficerType(){
      if(this.properties.deceasedPeople === '부모'){
        return '아들';
      } else if(this.properties.deceasedPeople === '조부모'){
        return '효손';
      } else if(this.properties.deceasedPeople === '백부모' || this.properties.deceasedPeople === '숙부모'){
        return '조카';
			} else if(this.properties.deceasedPeople === '장인장모') {
        return '사위';
      }
      return '';
    },
    getCongratulatoryOfficerName(){
      if(this.properties.congratulatoryOfficerName === ''){
        return 'ㅇㅇㅇ';
      } else {
        return this.properties.congratulatoryOfficerName;
      }
    },
    getDeceasedName(){
      if(this.properties.deceasedName === ''){
        return 'ㅇㅇㅇ';
      } else {
        return this.properties.deceasedName;
      }
    },
    getDeceasedPaperListName(){
      if(this.properties.sex === 'male'){
        return this.getFirstPersonPaperListName();
      } else {
        return this.getSecondPersonPaperListName();
      }
    },
    getFirstPersonPaperListName(){
      if(this.properties.person1.paperListNameSuffix === ''){
        return 'ㅇㅇㅇ';
      } else {
        return this.properties.person1.paperListNameSuffix.split(".")[2];
      }
    },
    getSecondPersonPaperListName(){
      if(this.properties.person2.paperListNameSuffix === ''){
        return 'ㅇㅇㅇ';
      } else {
        return this.properties.person2.paperListNameSuffix.split(".")[2];
      }
    }
