      } else if (paperListType === '장인장모') {
        this.appellation = '外甥.외생'; // 호칭
        this.person1.paperListNameSuffix = '室顯考.실현고.장인'
        // this.person1.paperListNamePrefix = '學生府君.학생부군'
        this.person2.paperListNameSuffix = '室顯妣.실현비.장모님'
        this.person2.paperListNamePrefix = '孺人.유인'
      }
    },
    changePaperListPrefix(paperListNamePrefix) {
        if (paperListNamePrefix === '학생') {
          this.person1.paperListNamePrefix = '學生府君.학생부군'
        } else if (paperListNamePrefix === '교장') {
          this.person1.paperListNamePrefix = '校長府君.교장부군'
        } else if (paperListNamePrefix === '서기관') {
          this.person1.paperListNamePrefix = '書記官府君.서기관부군'
        } else if (paperListNamePrefix === '사무관') {
          this.person1.paperListNamePrefix = '事務官府君.사무관부군'
        } else if (paperListNamePrefix === '이사관') {
          this.person1.paperListNamePrefix = '理事官府君.이사관부군'
        } else if (paperListNamePrefix === '감사관') {
          this.person1.paperListNamePrefix = '監査官府君.감사관부군'
        } else if (paperListNamePrefix === '장군') {
          this.person1.paperListNamePrefix = '將軍府君.장군부군'
        } else if (paperListNamePrefix === '관리관') {
          this.person1.paperListNamePrefix = '管理官府君.관리관부군'
        }
    },
    isTargetCongratulation(congratulationType, languageType){
      return this.congratulationType === congratulationType && this.languageType === languageType
    },
    getCongratulationProps(){
      var yearMonthDay = this.person1.yearMonthDay.split("-")
      var ganjiDate = solarToLunar(yearMonthDay[0], yearMonthDay[1], yearMonthDay[2])
      const props = {
        deceasedPerson: this.paperListType,
        deceasedName: this.deceasedName,
        appellation: this.appellation, // 호칭
        congratulatoryOfficerName: this.congratulatoryOfficerName, // 제주. 축관 이름
        paperListNameSuffix: this.person1.paperListNameSuffix, // 지방
        paperListNamePrefix: this.person1.paperListNamePrefix, // 지방
        surName: this.person1.surName, // 성
        ganjiDate: ganjiDate,
        yearMonthDay: yearMonthDay
      }
      console.log('result: ', props)
      return props
    },
    getGroupCongratulationProps(){
      var yearMonthDay = this.person1.yearMonthDay.split("-")
      var ganjiDate = solarToLunar(yearMonthDay[0], yearMonthDay[1], yearMonthDay[2])
      const props = {
        sex: this.sex,
        deceasedPeople: this.paperListType,
        appellation: this.appellation, // 호칭
        congratulatoryOfficerName: this.congratulatoryOfficerName, // 제주. 축관 이름
        person1: this.person1,
        person2: this.person2,
        ganjiDate: ganjiDate,
        yearMonthDay: yearMonthDay
      }
      console.log('result: ', props)
      return props
    },
    isNeededPaperListNamePrefix() {
      return this.languageType === 'Man'; 
    },
    isNeededDeceasedName() {
      return (this.paperListType === '아들' && this.languageType === 'Kor') || 
              (this.paperListType === '아우' && this.languageType === 'Kor');
    },
    isNeededSurName() {
      var paperListName = null;
      if(this.congratulationType !== 'GroupGijesachuk') {
        paperListName = this.person1.paperListNameSuffix.split(".")
        return paperListName[2] === '어머님' || paperListName[2] === '할머님' ||
                paperListName[2] === '증조할머님' || paperListName[2] === '고조할머님' || paperListName[2] === '아내'
      }
      return this.languageType === 'Man'
    },
    searchSurName(){
      const parameter = {
        keyword: this.keyword
      }
      this.$store.dispatch('REQUEST_SUR_NAME_LIST', parameter)
    },
    selectSurName(surName) {
      this.selectedSurNameId = surName.id
      if(this.congratulationType === 'GroupGijesachuk') {
        this.person2.surName = surName.surNameMandarin + '.' + surName.surNameKorean
      } else {
        this.person1.surName = surName.surNameMandarin + '.' + surName.surNameKorean
      }
    },
    saveTablet() {
      
      const contentTxt = this.$refs.printWrapId.innerHTML

      const parameter = {
        tablet: {
          tablet3CheckedLanguage:  this.checkedLanguage,
          tablet3Keyword:  this.keyword,
          tablet3Sex:  this.sex,
          tablet3LanguageType:  this.languageType,
          tablet3CongratulationType:  this.congratulationType,
          tablet3DeceasedName:  this.deceasedName,
          tablet3TempPaperListType:  this.tempPaperListType,
          tablet3PaperListType:  this.paperListType,
          tablet3TempPaperListNamePrefix:  this.tempPaperListNamePrefix,
          tablet3Appellation:  this.appellation,
          tablet3CongratulatoryOfficerName:  this.congratulatoryOfficerName,
          tablet3PaperListNameSuffix: this.person1.paperListNameSuffix ,
          tablet3PaperListNamePrefix:this.person1.paperListNamePrefix ,
          tablet3SurName:this.person1.surName,
          tablet3PaperListNameSuffix2: this.person2.paperListNameSuffix,
          tablet3PaperListNamePrefix2:this.person2.paperListNamePrefix,
          tablet3SurName2:this.person2.surName,

          tabletType: { codeId: 'TABLET-2' }
        }
      }
      this.$store.dispatch("REGIST_TABLET3", parameter);
    }
  }
}
</script>